export function addClass(el, className) {
  // console.log(hasClass(el, className))
  if (hasClass(el, className)) { // 检查是否有这个className了
    return
  }
  let newClass = el.className.split(' ') // 将dom的className按空格分割成数组
  newClass.push(className) // 将新的ClassName加入到数组
  el.className = newClass.join(' ') // 将加入新ClassName的数组用空格拼接成字符串
}

export function hasClass(el, className) {
  let reg = new RegExp('(^|\\s)' + className + '(\\s|$)')
  return reg.test(el.className)
}

export function getData(el, name, val) {
  const prefix = 'data-'
  name = prefix + name
  if (val) {
    return el.setAttribute(name, val)
  } else {
    return el.getAttribute(name)
  }
}

let elementStyle = document.createElement('div').style

let vendor = (() => {
  let transformNames = {
    webkit: 'webkitTransform',
    Moz: 'MozTransform',
    O: 'OTransform',
    ms: 'msTransform',
    standard: 'transform'
  }

  for (let key in transformNames) {
    if (elementStyle[transformNames[key]] !== undefined) {
      return key
    }
  }
  return false
})()

export function prefixStyle(style) {
  if (vendor === false) {
    return false
  }
  if (vendor === 'standard') {
    return style
  }
  return vendor + style.charAt(0).toUpperCase() + style.substr(1) // 返回厂商名 + style第一个字母大写 + style其他字母
}



